@font-face {
    font-family: roboto-condenced;
    src: url(./Assets/RobotoCondensed-Regular.ttf);
}
@font-face {
    font-family: economica-bold;
    src: url(./Assets/Economica-Bold.ttf);
}
body{
    text-align: center;
    font-size: 20px;
    margin: 20px;
    font-family: ' roboto-condenced';
}

h1,h2,h3,h4,h5{
    font-family:  economica-bold;
}

.title-fade-out{
    opacity: 0;
    animation-name: fade-out;
    animation-duration: 2s;
}

.title-fade-in{
    opacity: 100;
    animation-name: fade-in;
    animation-duration: 2s;
}
@keyframes fade-out {
    from {opacity:100}
    to {opacity: 0;}
}
@keyframes fade-in {
    from {opacity:0}
    to {opacity: 100;}
}